import Vue from 'vue'
import Vuex from 'vuex'
import '../setup'
import defaultValues from './default.json'

import normalizator, { fetch } from '../../src'
import { user, comment, article } from '../../schemas'

Vue.use(Vuex)

const normalizatorPlugin = normalizator({
  users: user,
  comments: comment,
  articles: article,
})

export default new Vuex.Store({
  modules: {
    articles: {
      state: {},
      mutations: {
        ADD_DATA(state, payload) {
          Vue.set(state, 'articleIds', payload)
        },
      },
      actions: {
        async fetchFirstData({ commit }) {
          const response = await fetch(
            {
              method: 'get',
              url: '/data1',
            },
            [article],
          )

          commit('ADD_DATA', response)
        },
        async fetchSecondData({ commit }) {
          const response = await fetch(
            {
              method: 'get',
              url: '/data2',
            },
            article,
          )
        },
      },
      getters: {
        getEntitiesState: (state: any, getters: any, rootState: any, rootGetters: any) => {
          return rootState
        },
        getCommentsForArticle: (state: any, getters: any, rootState: any, rootGetters: any) => (id: string) => {
          const targetArticle = rootState.n_entities.articles[id]

          if (!targetArticle) {
            return []
          }

          return rootGetters.nGetItems('comments', targetArticle.comments)
        },
      },
    },
  },
  plugins: [normalizatorPlugin],
})
